	module.exports = {"data":{"content":{"content":"<h2 id=為什麼會有這篇文章>為什麼會有這篇文章</h2>\n<p>打好基本功是很重要的。。。</p>\n<h2 id=最簡單的閉包>最簡單的閉包</h2>\n<ol>\n<li>先定義一個Function A</li>\n<li>在A的裡面定義一個Function B</li>\n<li>在A中返回B</li>\n<li>執行A(), 將A()的返回值賦予給b</li>\n<li>執行b()</li>\n</ol>\n<pre><code class=\"language-[js]\"><pre><code class=\"hljs [js]\">function A() {\n  function B() {\n    console.log(\"Hello, world!\");\n  }\n  return B;\n}\nvar b = A();\nb(); // Hello World\n</code></pre></code></pre>\n<h2 id=加法-ES6>加法 - ES6</h2>\n<ol>\n<li>先定義adder()</li>\n<li>定義匿名函數</li>\n<li>返回x + y</li>\n<li>定義變量 add5</li>\n<li>執行 add5()</li>\n</ol>\n<pre><code class=\"language-[js]\"><pre><code class=\"hljs [js]\">let adder = (x) => (y) => x + y\n\nlet add5 = adder(5);\nlet add10 = adder(10);\n\nconsole.log(add5(7)); // 12\nconsole.log(add10(7)); // 17\n</code></pre></code></pre>\n","title":"初探 JavaScript 的 Closure","createdDate":"2017-02-19T15:12+08:00","headerImgur":"KMksHxZ.png","id":"3NohHmHdRmgQQYI08UiIC8","toc":["為什麼會有這篇文章","最簡單的閉包","加法-ES6"],"tags":"JavaScript"},"latestPosts":{"totalCount":60,"edges":[{"node":{"title":"【網易考拉校招】部門總監面對面-直播答疑邀約（附網易內推碼B222E54）","url":"netease-kaola-live","createdDate":"2018-08-01T20:00+08:00"}},{"node":{"title":"Uncaught ReferenceError: regeneratorRuntime is not defined 解決方法","url":"regenerator-runtime-is-not-defined","createdDate":"2018-07-29T20:00+08:00"}},{"node":{"title":"關於作者","url":"about-calpa-liu","createdDate":"2018-05-01T20:00+08:00"}},{"node":{"title":"【方法】在直接複製貼上內容的時候，可能已經不知不覺注入了一段不可視的「空白」","url":"insert-zero-width-characters","createdDate":"2018-04-06T23:00+08:00"}},{"node":{"title":"知乎無限 live 數據流網站","url":"zhihu-unlimited-lives-website","createdDate":"2018-03-28T20:49+08:00"}},{"node":{"title":"Gatsbyjs async/await 更新報錯解決方法","url":"gatsybjs-async-await-upgrade-bug-solution","createdDate":"2018-03-21T20:00+08:00"}}]}},"pathContext":{"id":"3NohHmHdRmgQQYI08UiIC8"}}

